header{position:sticky;position:-webkit-sticky;top:0;background-color:#f8f9fa}header .top-menu{display:flex;justify-content:space-between;align-items:center}header .top-menu .logo{display:flex;align-items:center;padding:var(--padding-px-4)}header .top-menu .logo i{color:#212529;font-size:24px;padding:var(--padding-px-4)}header .top-menu .logo .title{color:#212529;font-family:'Do Hyeon';font-weight:bolder;margin-left:10px;font-size:32px}header .top-menu .logo .title .sub-title{color:black;font-size:12px;font-family:'Yeon Sung';margin:-6px 0px 0px 8px}header .top-menu .menu{padding:var(--padding-px-4);padding-right:10px;font-size:24px;color:#343a40}header .bottom-menu{font-size:9px;background-color:var(--oc-red-11);padding:var(--padding-px-10)}header .bottom-menu ul{display:flex;justify-content:space-around;align-items:center;color:white}header .bottom-menu ul li a:hover{border-bottom:2px solid white;padding-bottom:var(--padding-px-2)}header .gradient{height:6px;background:linear-gradient(to bottom, var(--oc-red-11), #f8f9fa)}
