.itemkind ul{display:flex;justify-content:flex-start}.itemkind ul li span{display:flex;flex-direction:column;justify-content:center;width:60px;height:60px;border:1px solid #adb5bd;text-align:center;font-size:20px;margin:1px;background-color:white;transition-property:border,margin,padding,color,font;transition-duration:var(--btn-transition-duration);transition-timing-function:ease-in}.itemkind ul li span:hover,.itemkind ul li input[type="radio"]:checked ~ label span{border:1.4px solid var(--oc-red-10);margin:1px;cursor:pointer;color:var(--oc-red-10);font-weight:600}.itemkind ul li label span div{font-size:8px;padding-top:4px}.filter-view ul{display:flex;flex-wrap:wrap;margin-bottom:4px}.filter-view ul li{margin-bottom:6px}.filter-view ul li label span{border-radius:var(--btn-border-radius-1);padding:8px 14px 8px 14px;font-size:12px;font-weight:800;margin-right:4px;cursor:pointer;background-color:#ced4da;transition-property:border,background,font;transition-duration:var(--btn-transition-duration);transition-timing-function:ease-in}.filter-view ul li label span:hover{background-color:#adb5bd}.filter-view ul li input[type="checkbox"]:checked ~ label span{background-color:var(--oc-red-10);color:#f8f9fa}
